.ias-bg {
  background: #15142b;
  color: #fff;
  padding-top: 60px;
  padding-bottom: 60px;
}
.ias-label {
  color: #fff;
  font-family: "Urbanist";
  font-size: 12px;
  font-weight: 400;
  line-height: normal;
  text-transform: uppercase;
}

.ias-title {
  color: #fff;
  font-family: "Urbanist";
  font-size: 40px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.accordion-item {
  border: none;
  background: transparent;
}
.accordion-button.ias-accordion-btn {
  border: none;
  border-radius: 0px !important;
 border-bottom: 0.646px solid rgba(255, 255, 255, 0.60);
background: linear-gradient(90deg, #150F22 -11.97%, #363539 100%);
  color: #fff;
  font-family: Urbanist;
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 39.6px;
  box-shadow: none;
  transition: background 0.35s cubic-bezier(0.4, 0, 0.2, 1), color 0.3s;
}

.accordion-button.ias-accent{
  background: linear-gradient(90deg, #492FB2 -1.74%, #ADADAD 108.02%);
//   color: #fff;
  border-bottom: 0px;
}
.ias-status {
  color: rgba(255, 255, 255, 0.43);
  font-family: "Urbanist";
  font-size: 22px;
  font-style: normal;
  font-weight: 400;
  line-height: 39.6px;
}

.ias-accordion-body {
  background: transparent;
  color: #fff;
  text-align: justify;
  font-family: "Urbanist";
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 25px;
  border-radius: 0px;
  transition: background 0.35s cubic-bezier(0.4, 0, 0.2, 1);
}
.ias-accent {
  background: linear-gradient(90deg, #492FB2 -1.74%, #ADADAD 108.02%) !important;
  color: #fff;
}
.accordion-button::after {
  display: none !important;
}
.chevron-icon {
  color: #fff;             // White color for icon
  fill: #fff;
  margin-left: auto;
  transition: transform 0.25s cubic-bezier(.4,0,.2,1);
}
.chevron-icon.rotate {
  transform: rotate(180deg); // Rotate when open
}
.btn-join{
  border-radius: 4px;
border: 1px solid #FFF;
background-color: #ffffff00;
opacity: 0.71;
padding: 8px 24px;
color: #FFF;
text-align: center;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 24px; /* 150% */
letter-spacing: 0.15px;
transition: all 0.3s ease-in-out;
}
.btn-join:hover{
  border-radius: 4px;
border: 1px solid #FFF;
background-color: #fff;
opacity: 0.71;
padding: 8px 24px;
color: #000;
text-align: center;
font-family: Urbanist;
font-size: 16px;
font-style: normal;
font-weight: 500;
line-height: 24px; /* 150% */
letter-spacing: 0.15px;
}